    yinlehaibaoChat1() {
      this.$nextTick(()=>{

        var yinlehaibaoChart1 = echarts.init(document.getElementById("yinlehaibaoChart1"),'dark');
        this.$http({
            url: "yinlehaibao/group/yinlefenlei",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.barNum){
						break;
					}
                    xAxis.push(res[i].yinlefenlei);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].yinlefenlei
                    })
                }
                var option = {};
				let titleObj = this.bar.title
				titleObj.text = ''
				
				const legendObj = this.bar.legend
				
				let xAxisObj = this.bar.xAxis
				xAxisObj.type = 'category'
				xAxisObj.data = xAxis
                
				
				let yAxisObj = this.bar.yAxis
				yAxisObj.type = 'value'
				
				let seriesObj = {
                        data: yAxis,
                        type: 'bar'
                    }
				seriesObj = Object.assign(seriesObj , this.bar.series)
				const gridObj = this.bar.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: '{b} : {c}'
				}
				tooltipObj = Object.assign(tooltipObj , this.bar.tooltip?this.bar.tooltip:{})
                option = {
					backgroundColor: this.bar.backgroundColor,
					color: this.bar.color,
                    title: titleObj,
					legend: legendObj,
                    tooltip: tooltipObj,
                    xAxis: xAxisObj,
                    yAxis: yAxisObj,
					grid: gridObj,
                    series: [seriesObj]
                };
                // 
                yinlehaibaoChart1.setOption(option);
				

                  //
				window.addEventListener('resize', () => {
                    yinlehaibaoChart1.resize();
                },false);
            }
        });
      })
    },

